import {
  Container,
  Box,
  Typography,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  useMediaQuery,
} from "@material-ui/core";
import React from "react";
import BannerImage from "../../image/banner.png";
import { useTheme } from "@material-ui/core/styles";

function Banner() {
  const [open, setOpen] = React.useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div
      className="banner"
      style={{
        backgroundImage: `url(${BannerImage})`,
      }}
    >
      <Container className="bannerCardContainer">
        <Box className="bannerCardBox">
          <div className="bannerCard">
            <div className="bannerCardPadding">
              <Typography variant="h3" className="fontPoppins bannerCardTopHeading">
                COVID-19 DIAGNOSTICS
              </Typography>
              <Typography variant="body2" className="fontPoppins bannerCardSubHeading">
                High quality, affordable and accessible medical devices and diagnostic test kits.
              </Typography>
              <Button className="bannerButton" onClick={handleClickOpen}>
                Enquire Now
              </Button>
            </div>
          </div>
        </Box>
        <div>
          <Dialog fullScreen={fullScreen} open={open} onClose={handleClose} aria-labelledby="responsive-dialog-title">
            <DialogTitle id="responsive-dialog-title">Enquiry Form</DialogTitle>
            <DialogContent>
              <DialogContentText>
                Let Google help apps determine location. This means sending anonymous location data to Google, even when
                no apps are running.
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button autoFocus onClick={handleClose} color="primary">
                Disagree
              </Button>
              <Button onClick={handleClose} color="primary" autoFocus>
                Agree
              </Button>
            </DialogActions>
          </Dialog>
        </div>
      </Container>
    </div>
  );
}

export default Banner;
